<template>
    <div class="Admin" v-loading="$store.state.loading">
        <div class="AdminTop">
            <i>当前位置：首页</i> >
            <span>部门管理</span>
        </div>
        <div class="content">
            <div class="AdminContent clearfix">
                <div class="search clearfix">
                    <div class="searchOne">
                        <el-select v-model="value" placeholder="请选择">
                            <el-option
                                v-for="(item,index) in Choice"
                                :key="index"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="ipt">
                    <input type="text" placeholder="请输入查询的关键字" v-model="content" @keyup.enter.native="search(1)">
                    <button @click="search(value)"><i class="el-icon-search"></i>查询</button>
                </div>
                <div class="btn">
                    <el-upload
                        style="width: 130px;float: left;"
                        action="/api/section-import"
                        :on-remove="handleRemove"
                        multiple
                        :limit="1"
                        :on-exceed="handleExceed"
                        accept="application/xlsx"
                        auto-upload
                        >
                        <!--:file-list="fileList"-->
                        <!--<el-button size="small" type="primary">点击上传</el-button>-->
                        <button ><i class="el-icon-edit-outline"></i>Excel导入</button>
                    </el-upload>
                    <!--<button><i class="el-icon-edit-outline"></i>Excel导入</button>-->
                    <button  @click="show">添加部门</button>
                </div>
                <div class="model" v-show="isShow">
                    <div class="box">
                        <div class="boxHeader">
                            <span>添加部门</span>
                            <i class="el-icon-close" @click="boxHide"></i>
                        </div>
                        <div class="boxIpt">
                            <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                                <el-form-item label="地区" prop="region"><b>*</b>
                                    <el-input v-model="ruleForm.region"></el-input>
                                </el-form-item>
                                <!-- <el-form-item label="单位" prop="pass"><b>*</b>
                                <el-input type="pass" v-model="ruleForm.pass" auto-complete="off"></el-input>
                                </el-form-item> -->
                                <el-form-item label="部门" prop="dept"><b>*</b>
                                    <el-input type="pass" v-model="ruleForm.dept" auto-complete="off"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="modelBtn">
                            <button @click="submitForm('ruleForm')">保存</button>
                        </div>
                    </div>
                </div>
                <div class="modelEdit" v-show="EditShow">
                    <div class="box">
                        <div class="boxHeader">
                            <span>编辑部门</span>
                            <i class="el-icon-close" @click="EditHide"></i>
                        </div>
                        <div class="boxIpt">
                            <el-form :model="EditForm" status-icon :rules="rules" ref="EditForm" label-width="100px" class="demo-ruleForm">
                                <el-form-item label="地区" prop="section_one"><b>*</b>
                                    <el-input v-model="EditForm.section_one"></el-input>
                                </el-form-item>
                                <!-- <el-form-item label="单位" prop="pass"><b>*</b>
                                <el-input type="pass" v-model="ruleForm.pass" auto-complete="off"></el-input>
                                </el-form-item> -->
                                <el-form-item label="部门" prop="section_two"><b>*</b>
                                    <el-input type="pass" v-model="EditForm.section_two" auto-complete="off"></el-input>
                                </el-form-item>
                            </el-form>
                        </div>
                        <div class="modelBtn">
                            <button @click="submitEditForm('EditForm')">保存</button>
                        </div>
                    </div>
                </div>
            </div>
            <div class="listTable">
                <table>
                    <tr>
                        <td>地区</td>
                        <td>部门</td>
                        <td>管理</td>
                    </tr>
                    <tr v-for="(v,index) in lists" :key="index">
                        <td>{{v.section_one}}</td>
                        <td>{{v.section_two}}</td>
                        <td>
                            <span @click="showEdit(v.id)">编辑</span><span>|</span>
                            <span @click="deltPer(v.id)">删除</span>
                        </td>
                    </tr>
                </table>
            </div>
            <p v-if="myShow" style="font-size: 16px;text-align: center;margin-top:40px;">暂无数据</p>
            <!--分页-->
            <div class="page">
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="10"
                        background
                        layout="total,prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
