<script>
export default {
    data() {
        return {
            myShow: false, //是否有显示的数据
            // 删除确认框
            dialogVisible: false,
            // 表单
            ruleForm: {
                region: "",
                dept: ""
            },
            EditForm: {
                section_one: "",
                section_two: ""
            },
            rules2: {
                region: [{ required: true, message: "请输入地区", trigger: "blur" }],
                dept: [{ required: true, message: "请输入部门", trigger: "blur" }]
            },
            rules: {
                section_one: [
                    { required: true, message: "请输入地区", trigger: "blur" }
                ],
                section_two: [
                    { required: true, message: "请输入部门", trigger: "blur" }
                ]
            },
            isShow: false,
            EditShow: false,
            currentPage: 1,
            content: "", //查询输入的关键词
            total: 0, //部门总数
            addRegions: [], //添加地区选择
            lists: [],
            //筛选条件
            Choice: [
                {
                    value: "部门",
                    label: "部门"
                },
                {
                    value: "地区",
                    label: "地区"
                }
            ],
            value: "地区",
            deptId: ""
        };
    },
    methods: {
        //添加部门信息保存
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.$axios.post("/api/section-add", {
                        section_one: this.ruleForm.region,
                        section_two: this.ruleForm.dept
                    }).then((res)=> {
                    //              console.log(res);
                        if (res.data.message == "成功") {
                        this.$message({
                            message: "添加成功",
                            type: "success"
                        });
                        this.isShow = !this.isShow;
                        this.getDepartment(this.currentPage);
                        this.resetForm("ruleForm");
                        }
                    }).catch((error)=> {
                    //              console.log(error);
                    });
                } else {
                //          console.log("error submit!!");
                    return false;
                }
            });
        },

        resetForm(formName) {
        	this.$refs[formName].resetFields();
        },

        // 编辑部门信息
        submitEditForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.$axios.post("/api/section-save", {
                        id: this.deptId,
                        section_one: this.EditForm.section_one,
                        section_two: this.EditForm.section_two
                    }).then((res)=> {
                    //              console.log(res);
                        if (res.data.message == "成功") {
                            this.$message({
                                message: "成功",
                                type: "success"
                            });
                            this.EditShow = !this.EditShow;
                            this.getDepartment(this.currentPage);
                            this.resetForm("EditForm");
                        }
                    }).catch((error)=> {
                    //              console.log(error);
                    });
                } else {
                //          console.log("error submit!!");
                    return false;
                }
            });
        },

        // 获取部门
        getDepartment(val) {
            this.$axios.post("/api/section-list", {
                number: 10,
                page: val
            }).then((res)=> {
            // console.log(res)
            for (var i in res.data.data.data) {
                this.total = res.data.data.total;
            }
            this.lists = res.data.data.data;
            this.$store.state.loading = false;
            }).catch((error)=> {
                console.log(error);
            });
        },

        //去重函数
        unique(array) {
            var obj = {};
            return array.filter(function(item, index, array) {
            return obj.hasOwnProperty(typeof item + JSON.stringify(item))
            ? false
            : (obj[typeof item + JSON.stringify(item)] = true);
            });
        },

        //删除部门信息
        deltPer(valId) {
            this.$confirm("此操作将永久删除该部门, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$axios.post("/api/section-del", {
                    id: valId
                }).then((res)=> {
                    if (res.data.message === "成功") {
                        this.$message({
                            message: "删除成功",
                            type: "success"
                        });
                        this.getDepartment(this.currentPage);
                    } else if(res.data.status == 422) {
                        this.$message.error("无法删除，部门下有相关人员!");
                    }
                }).catch((error)=> {
                    console.log(error);
                });
            }).catch(() => {
                this.$message({
                    type: "warning",
                    message: "已取消删除"
                });
            });
        },

        // 分页
        handleSizeChange(val) {},
        handleCurrentChange(val) {
            this.$store.state.loading = true;
            this.currentPage=val;
            this.search(val);
        },

        // 模态框
        show() {
        	this.isShow = !this.isShow;
        },

        boxHide() {
            this.isShow = !this.isShow;
            this.resetForm("ruleForm");
        },

        // 显示模态框
        showEdit(val) {
            this.EditShow = !this.EditShow;
            this.$axios.post("/api/section-list", {
                id: val
            }).then((res)=> {
                // console.log(res)
                this.EditForm = res.data.data.data[0];
                this.deptId = val;
            }).catch((error)=> {
                console.log(error);
            });
        },

        EditHide() {
            this.EditShow = !this.EditShow;
            this.resetForm("EditForm");
        },

        //搜索查询
        search(val) {
            this.lists = [];
            let paramy = { number: 10, page: val };
            if (val == "地区") {
                if (this.content) {
                    this.$set(paramy, "section_one", this.content);
                }
            } else if(val == "部门"){
                if (this.content) {
                    this.$set(paramy, "section_two", this.content);
                }
            }
            let dep = [];
            this.$axios.post("/api/section-list", paramy).then((res)=> {
                if(res.data.data!=null){
                    this.lists=res.data.data.data;
                    this.total = res.data.data.total;
                    this.myShow = false;
                    for(var i in res.data.data.data){
                        dep.push(res.data.data.data[i].section_two);
                    }
                    this.departs=this.unique(dep);
                }else{
                    this.myShow = true;
                    this.total=0;
                }
                this.lists = res.data.data.data;
                this.$store.state.loading = false;
            }).catch((error)=> {
                console.log(error);
            });
        },

        //确定移除xlsx
        handleRemove(file, fileList) {
        //		  console.log(file, fileList);
        },

        //再次添加文件的提示
        handleExceed() {
            this.$message.warning(`当前限制选择 1 个文件`);
        },
    },
    created() {
        this.$store.state.loading = true;
        this.getDepartment();
    },
};
</script>
